                tableData: [],
                cur_page: 1,
                size: 5,
                totalRows: 10,
                select_word: '',
                is_search: false
            }
        },
        created(){
            this.getData();
        },
        computed: {
            data(){
                const self = this;
                return self.tableData.filter(function(d) {
                    if (d.name.indexOf(self.select_word) > -1 || d.deptType.indexOf(self.select_word) > -1 || d.description.indexOf(self.select_word) > -1) {
                        return d;
                    }
                })
            }
        },
        methods: {
            handleCurrentChange(val){
                this.cur_page = val;
                this.getData();
            },
            getData(){
                let self = this;
                if(process.env.NODE_ENV === 'production'){
                    self.url = 'http://192.168.18.205/exam-api/exam/page';
                } else {
                    self.url = '../../static/data.json';
                }
                self.$axios({
                    method: 'get',
                    url: self.url,
                    params: {offset: self.cur_page, size: self.size}
                })
                .then(function (res) {
                    console.log(res.data)
                    self.size = res.data.size;
                    self.totalRows = res.data.totalRows;
                    self.tableData = res.data.resources;
                })
                // self.$axios.post(self.url, {page:self.cur_page}).then((res) => {
                //     self.tableData = res.data.list;
                // })
            },
            search(){
                this.is_search = true;
            },
            formatter(row, column) {
                return row.address;
            },
            filterTag(value, row) {
                return row.tag === value;
            },
            handleEdit(index, row) {
                this.$message('编辑第'+(index+1)+'行');
            },
            handleDelete(index, row) {
                this.$message.error('删除第'+(index+1)+'行');
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }
